node-element {
  width: 12rem;
  background-color: #1b1d1e;
  padding: 0.3rem;
  border: solid 2px #111;
  border-radius: 0.5rem;
  color: #9e9e9e;
}

node-element.selected {
  box-shadow: 0px 0px 10px 0px navajowhite;
}

node-element.output {
  background-color: #173748;
}

node-element .node-header {
  font-weight: bold;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

node-element .node-title {
  margin-right: auto;
  cursor: pointer;
}

node-element .node-collapse {
  display: inline-flex;
  align-items: center;
  font-size: 0.6rem;
  padding-left: 0.2rem;
  cursor: pointer;
}

node-element .node-collapse.displayed::before {
  content: "▼";
}

node-element .node-collapse.collapsed::before {
  content: "▲";
}

node-element .io-line {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0.4rem 0;
}

node-element .input-text {
  font-size: 0.6rem;
  text-transform: uppercase;
  margin-left: 0.3rem;
}

node-element .output-text {
  font-size: 0.6rem;
  text-transform: uppercase;
  margin-right: 0.3rem;
}
