nodes-container .output-anchor circle {
  /* Modify call to plumb.addEndpoint to edit UI */
  fill: #333;
  r: 6px;
}

nodes-container .input-anchor circle {
  /* Modify call to plumb.addEndpoint to edit UI */
  stroke: black;
  fill: white;
  stroke-width: 3px;
  r: 4px;
}

nodes-container .jtk-connector path {
  stroke-width: 4px;
}

nodes-container .jtk-connector.jtk-hover {
  cursor: pointer;
}
