node-element uniform-dropdown {
  display: inline-flex;
  align-items: center;
  height: 100%;
}

node-element uniform-dropdown select {
  background-color: rgba(0, 0, 0, 0.1);
  font-size: 0.9rem;
  width: 100%;
  color: white;
  text-align: right;
  border: none;
}

node-element uniform-dropdown select:focus-visible {
  outline: none;
}
