node-element uniform-number {
  display: flex;
  margin-right: 0.5rem;
  height: 100%;
}

node-element uniform-number input {
  background: transparent;
  border: none;
  cursor: pointer;
  text-align: right;
  width: 100%;
  color: white;
  font-size: 0.8rem;
}

node-element uniform-number input:focus-visible {
  outline: none;
  border-bottom: solid 1px black;
}
